/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #71B100;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.inicio {
  width: 100%;
  height: 550px;
  margin-right: auto;
  margin-left: auto;
  animation: inicio 30s infinite linear alternate;
  background-size: 100% 100%;
  position: relative;
  display: flex;
  justify-content: center;
}

@keyframes inicio {
  0%, 24% {
    background-image: url(../img/header.jpg);
  }
  25%, 40% {
    background-image: url(../img/header2.jpg);
  }
  41%, 60% {
    background-image: url(../img/header3.jpg);
  }
  61%, 80% {
    background-image: url(../img/header4.jpg);
  }
  81%, 100% {
    background-image: url(../img/interior.jpg);
  }
}
.footer {
  padding: 2.5rem 0;
  background-color: #71B100;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
@media (min-width: 768px) {
  .footer .contenedor-footer {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    padding-top: 10px;
  }
  .footer .contenedor-footer h3 {
    text-align: center;
    font-weight: 900;
  }
}
.footer a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.7rem;
  padding: 0.8rem;
  text-align: center;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer .contacto {
  text-align: left;
  padding: 1.2rem;
  box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  word-wrap: break-word;
  background-clip: border-box;
  border: 1px solid rgba(0, 0, 0, 0.125);
}
.footer .contacto p, .footer .contacto h1 {
  color: #FFFFFF;
}
.footer :root {
  --facebook: #3b5999;
  --instagram: #e4405f;
  --twitter: #55acee;
}
.footer .redes-container {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  padding-bottom: 10px;
}
.footer .redes-container ul h3 {
  font-size: 4rem;
  font-weight: bolder;
  text-align: center;
  -webkit-text-stroke: 0.8px rgba(0, 0, 0, 0.842);
  color: #FFFFFF;
}
.footer .redes-container ul li {
  display: inline-block;
  margin: 0 5px 5px;
  width: 50px;
  height: 50px;
  text-align: center;
}
.footer .redes-container ul li a {
  display: block;
  padding: 15 16px;
  background: #333;
  color: #fff;
  font-size: 18px;
  box-shadow: 0 3px 5px 0px rgba(0, 0, 0, 0.2);
  transition: 0.2s;
}
.footer .redes-container ul:hover a {
  filter: blur(2px);
}
.footer .redes-container ul li a:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 5px 5px 0px rgba(0, 0, 0, 0.4);
  filter: none;
}
.footer .whatsapp {
  margin-top: 10px;
}
.footer .whatsapp i {
  cursor: pointer;
  margin: 5px;
  color: #FFFFFF;
  background-color: green;
  font-size: 3rem;
  border-radius: 80%;
  box-shadow: 0 3px 5px 0px rgba(0, 0, 0, 0.89);
  padding: 2rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .footer .whatsapp i {
    font-size: 6rem;
  }
}
.footer .whatsapp i:hover {
  background-color: #FFFFFF;
  color: green;
}

.redes-container ul li .facebook:hover {
  background-color: #3b5999;
}

.redes-container ul li .instagram:hover {
  background-color: #e4405f;
}

.redes-container ul li .twitter:hover {
  background-color: #55acee;
}

