  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.6rem;
  display: block;
  padding-right: 10px;
  -webkit-text-stroke: 1px rgba(0, 0, 0, 0.842);
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
}

