.contenido-header {
  /* End Keyframes */
}
.contenido-header .hero {
  visibility: hidden;
}
@media (min-width: 768px) {
  .contenido-header .hero {
    visibility: visible;
  }
}
.contenido-header .hero h1 {
  display: block;
  width: fit-content;
  font-size: 4rem;
  position: relative;
  color: transparent;
  animation: text_reveal 0.5s ease forwards;
  animation-delay: 1s;
}
.contenido-header .hero h1 span {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background-color: #71B100;
  animation: text_reveal_box 1s ease;
  animation-delay: 0.5s;
}
.contenido-header .hero h1:nth-child(1) {
  animation-delay: 1s;
}
.contenido-header .hero h1:nth-child(2) {
  animation-delay: 2s;
}
.contenido-header .hero h1:nth-child(3) {
  animation: text_reveal_name 0.5s ease forwards;
  animation-delay: 3s;
}
.contenido-header .hero h1:nth-child(1) span {
  animation-delay: 0.5s;
}
.contenido-header .hero h1:nth-child(2) span {
  animation-delay: 1.5s;
}
.contenido-header .hero h1:nth-child(3) span {
  animation-delay: 2.5s;
}
.contenido-header .cta {
  display: inline-block;
  padding: 10px 30px;
  color: white;
  background-color: transparent;
  border: 2px solid #71B100;
  font-size: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  margin-top: 30px;
  transition: 0.3s ease;
  transition-property: background-color, color;
  -webkit-text-stroke: 0.6px rgba(0, 0, 0, 0.842);
}
.contenido-header .cta:hover {
  color: white;
  background-color: #71B100;
}
@keyframes text_reveal_box {
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}
@keyframes text_reveal {
  100% {
    color: white;
    -webkit-text-stroke: 0.6px rgba(0, 0, 0, 0.842);
  }
}
@keyframes text_reveal_name {
  100% {
    color: #71B100;
    font-weight: 500;
    -webkit-text-stroke: 0.6px rgba(0, 0, 0, 0.842);
  }
